<script>

// http://localhost:8082/dept/info/10
import axios from "axios";

const url = "http://localhost:8080/survey/form/1001";
export default {
  data() {
    return { tableData: {} , top1:"",top2:"",top3:"",stuId:""};
  },
  mounted() {
    axios
      .get(url)
      .then((response) => (this.tableData = response.data))
      .catch(function (error) {
        console.log(error);
      });
    
  },
  methods: {
      submit() {
        this.tableData.res.top1 = this.top1;
        this.tableData.res.top2 = this.top2;
        this.tableData.res.top3 = this.top3;
        this.tableData.res.stuId = this.stuId;
        //提交方式。
        console.log("发送的数据为：" + JSON.stringify(this.tableData.res));
        axios({
          url: "http://localhost:8080/survey/form/1001", 
          method: "post", 
          data: this.tableData.res
        }).then(function(response) {
            console.log(response);
        });
        // this.$http.post(
        //   "http://localhost:8080/survey/form1/1001",
        //   {form :this.tableData.res}
        // ).then(function(res){
        // //console.log 可以从浏览器控制台看到 我返回的是状态码 如果为404 405..可以在浏览器控制台看到。
        //       this.$alert("提交成功")
        //       console.log(res);
        // });
        console.log('submit!');
    }
  },
};
</script>

